.info__image
{
  margin: 60px auto 0;
  padding: 0;

  width: 120px;
  height: 120px;
  
  background-position: center;
  background-repeat: no-repeat;

  background-size: cover;
  overflow: hidden;
}

@media screen and (max-width: 460px)
{
  .info__image
  {
    margin-top: 50px;
  }
}
