.profile__name
{
  grid-area: title;

  padding: 0;
  margin: 0;

  text-align: left;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
}

@media screen and (max-width: 425px)
{
  .profile__name
  {
    text-align: center;
    font-size: 27px;
    line-height: 100%;
  }
}
