.header
{
  padding: 0 0 41px;
  margin: 45px auto 0;

  max-width: 880px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;  
  align-items: flex-end;

  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 425px)
{
  .header
  {
    margin-top: 28px;
    max-width: 320px;
  }
}
